      .col
        .p-4
          p.mb-0 Mucha gente limpia el teclado dndole la vuelta y sacudindolo. Un mtodo ms eficiente es utilizar aire comprimido. El aire comprimido es aire comprimido contenido en una lata con una boquilla muy larga. Para limpiar un teclado con aire comprimido, apunte entre las teclas y elimine todo el polvo y la suciedad. Tambin se puede usar una aspiradora, pero asegrese de que el teclado no tenga teclas sueltas que puedan ser succionadas por la aspiradora.

