            p.mb-0 Limpie la superficie de un escner de superficie plana rociando un limpiador de ventanas sobre una toalla de papel o un pao de algodn y limpie el vidrio hasta que quede limpio. 
        .col-xl-3.col-lg-4.col-8.mb-lg-0.mb-4(data-aos="flip-up")
