    separador
    #t_3_2.titulo-segundo.color-acento-botones(data-aos="fade-right")
      h2 3.2 Productos
    
    figure(data-aos="fade-left")
      img(src='@/assets/curso/tema3/img54.png' alt="Imagen decorativa")
    .row.justify-content-center
      .col-lg-10.img-02
        .tarjeta.BG11.p-4.mb-4
          p.text-center.mb-0 La limpieza fsica de su PC con regularidad puede agregar aos a su vida til general y ayudar a mantener sus componentes funcionando de la manera ms eficiente posible. Para ayudarlo a hacer bien el trabajo, a continuacin, le indicamos cmo limpiar su computadora.
    
    .row.mb-5
      .col-auto(data-aos="fade-right")
        .BGS01.px-5.py-2
          .h3.mb-0 Herramientas y suministros de limpieza
    
    p.mb-5(data-aos="fade-right") Al limpiar su computadora, es til tener a mano las herramientas y los suministros adecuados. Esto es lo que necesitar:

    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema3/img55.png' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        AcordionA(tipo="a" clase-tarjeta="tarjeta BG02")
          div.px-2(titulo="Pao de microfibra")
            p.mb-0 Estos paos de limpieza especializados estn hechos de un material suave y resistente. Los paos de microfibra no daan la pantalla ni la computadora, pero son excelentes para limpiar las reas delicadas.
          div.px-2(titulo="Alcohol isoproplico")
            p.mb-0 Cuando necesite quitar pegatinas o limpiar cualquier parte de su PC que no pueda exponerse a la humedad, el alcohol isoproplico es perfecto. El potente lquido se seca sin dejar residuos y disuelve incluso las acumulaciones de suciedad ms molestas.
          div.px-2(titulo="Bastoncillos de algodn")
            p.mb-0 A veces llamados bastoncillos de algodn, los bastoncillos de algodn son perfectos para meterse en lugares delicados o difciles de alcanzar en su PC.
          div.px-2(titulo="Toallas de papel")
            p.mb-0 Si bien no son la solucin de limpieza ms elegante, las toallas de papel hacen el trabajo al limpiar el exterior de una computadora o cualquier gabinete con ventanas de vidrio.
          div.px-2(titulo="Limpieza externa")
            p El exterior de su computadora se puede limpiar antes o despus del interior.
            ul.lista-ul--color.lista-ul--separador
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | El Limpiador de vidrio rociado sobre una toalla de papel, nunca directamente sobre la PC, es la mejor manera de limpiar cualquier parte externa de su computadora.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Recuerde que los puertos en la parte delantera y trasera de la mquina son susceptibles a daos por humedad.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | El uso de un pao o una toalla de papel que est demasiado hmedo puede provocar que un exceso de limpiador entre en los puertos y cause un dao significativo a su PC.
          div.px-2(titulo="Retire los filtros de polvo")
            p Antes de limpiar su escritorio, asegrese de verificar si hay filtros de polvo extrables en su carcasa.
            ul.lista-ul--color.lista-ul--separador
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | A menudo, habr al menos un filtro conectado a la fuente de alimentacin que se puede quitar y limpiar fcilmente. 
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Los filtros de polvo son increbles para reducir la acumulacin de polvo en su PC, pero pueden tener el efecto contrario cuando intenta limpiar o quitar el polvo del interior de su mquina. 
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | La misma tecnologa que evita que entre polvo tambin evitar que los desechos salgan de la caja, por lo que es mejor quitar los filtros al principio del proceso de limpieza.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        AcordionA(tipo="a" clase-tarjeta="tarjeta BG02")
          div.px-2(titulo="Limpieza interna")
            p Hay algunas reas clave que debe limpiar regularmente dentro de su PC.
            ul.lista-ul--color.lista-ul--separador
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Recuerde tomar su tiempo y concentrarse en limpiar cuidadosamente cuando trabaje cerca de componentes expuestos.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | La mayora de las computadoras de escritorio modernas tienen suficiente espacio en su estuche para limpiarlas cmodamente sin necesidad de desmontarlas.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Para ser increblemente minucioso, puede quitar su tarjeta grfica, pero eso no es necesario.
          div.px-2(titulo="Limpieza de su casa")
            ul.lista-ul--color.lista-ul--separador
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Asegrese de estar en un espacio bien ventilado.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Use aire comprimido para eliminar primero las partculas ms grandes de polvo del interior de la carcasa.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Ciertas reas, como bahas de disco duro vacas o manojos de cables, pueden tener una acumulacin significativa de suciedad.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Si puede, quite las bridas para cables o vace los compartimientos para unidades para limpiar detrs de ellos ms fcilmente.
          div.px-2(titulo="Ventiladores de limpieza")
            ul.lista-ul--color.lista-ul--separador
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Los ventiladores tambin son la causa principal de la suciedad y el polvo que ingresan a su PC.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Los desechos se enganchan con el aire de admisin fro y se adhieren a los ventiladores y a cualquier otro componente que pueda tocar.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | El aire comprimido generalmente puede eliminar la mayor parte de la suciedad de los ventiladores de caja.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Sostener el ventilador cuando lo roca con aire comprimido protege los cojinetes del ventilador, evitando que gire ms rpido de lo diseado y se dae.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | A veces, el aire comprimido puede no ser suficiente para hacer el trabajo. Ah es donde los hisopos de algodn son tiles.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Con un bastoncillo de algodn nuevo, elimine con cuidado cualquier suciedad de los ventiladores que el aire comprimido no haya podido liberar.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Una vez que haya eliminado toda la acumulacin de polvo significativa de los ventiladores, golpee toda el rea con aire nuevamente para eliminar el polvo cado que acaba de limpiar el ventilador.
          div.px-2(titulo="Limpieza de su fuente de alimentacin")
            ul.lista-ul--color.lista-ul--separador
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Como se mencion anteriormente, la parte ms crucial de la limpieza de una fuente de alimentacin es quitar y limpiar adecuadamente el filtro de polvo antes de intentar limpiar el resto del dispositivo.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | La mayora de los componentes de una fuente de alimentacin estn ocultos dentro de la pieza, por lo que es poco lo que puede hacer adems de limpiar la cubierta antipolvo.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Usando rfagas intermitentes, puede usar aire comprimido para empujar el polvo desde el interior de la fuente de alimentacin hacia donde normalmente estara la cubierta antipolvo.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Nunca use ninguna solucin de limpieza en o cerca de una fuente de alimentacin.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ac
                | Puede usar alcohol isoproplico para quitar las pegatinas del dispositivo, pero aplquelo con cuidado con un bastoncillo de algodn y no lo deje correr.
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema3/img56.png' alt="Imagen decorativa")

    .row.mb-5
      .col-auto(data-aos="fade-right")
        .BGS01.px-5.py-2
          .h3.mb-0 Limpieza de su tarjeta grfica/GPU
    
    .row.align-items-center.mb-5
      .col-xl-3.col-lg-4.col-sm-6.col-8.img-06.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema3/img57.png' alt="Imagen decorativa")
      .col-xl-3.col-lg-2.d-lg-flex.d-none
      .col-xl-8.col-lg-10
        .BGR06.w-100.p-4.row.justify-content-center.align-items-center(data-aos="fade-right")
          .col-lg-1.d-lg-flex.d-none
          .col-lg-1.col-2
            img(src='@/assets/curso/temas/img02.svg' alt='Imagen decorativa')
          .col-lg-9.col-10
            .h4.mb-0 En estos das, la mayora de las tarjetas grficas tienen un conjunto de dos o incluso tres ventiladores para mantenerlas frescas.
        .BG07.w-100.p-4.row.justify-content-center.align-items-center(data-aos="fade-right")
          .col-lg-1.d-lg-flex.d-none
          .col-lg-1.col-2
            img(src='@/assets/curso/temas/img06.svg' alt='Imagen decorativa')
          .col-lg-9.col-10
            .h4.mb-0 Si bien esto es fantstico para juegos y otras tareas profesionales, crea ms aire en movimiento en su sistema y, por lo tanto, esparce ms polvo y escombros.

    .row.justify-content-center.mb-5
      .col-auto.BGS05.px-4.py-2(data-aos="fade-left")
        .h4.text-white.text-center.mb-0 Hay dos formas de limpiar la tarjeta grfica
    
    .row.justify-content-center.mb-5
      .col-lg-6.mb-lg-0.mb-4(data-aos="fade-right")
        .tarjeta.BG09.h-100
          .BG16.px-5.py-2
            .h4.text-white.text-center.mb-0 Retire y limpie
          .p-4
            ul.lista-ul--color.lista-ul--separador
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-s
                | Desconecte con cuidado la tarjeta grfica de su ranura PCI y sostngala firmemente con una mano.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-s
                | Con aire comprimido, limpie alrededor del disipador trmico y los puertos de la GPU, teniendo cuidado de eliminar todos los residuos.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-s
                | Recuerde sujetar los ventiladores mientras los limpia para evitar daar la tarjeta grfica.
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-s
                | Puede notar que sale algo de polvo de su GPU una vez que la reinstala y la enciende, pero eso es normal y se limpia fcilmente con otra rfaga rpida de aire comprimido.
      .col-lg-6.mb-lg-0.mb-4(data-aos="fade-left")
        .tarjeta.BG09.h-100
          .BG14.px-5.py-2
            .h4.text-center.mb-0 Sin quitar
          .p-4
            ul.lista-ul--color.lista-ul--separador
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ab
                | Si puede colocar su PC de lado, puede limpiar su tarjeta grfica de manera eficiente sin necesidad de quitarla. 
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ab
                | Siempre que pueda ver y acceder cmodamente a los ventiladores de su tarjeta, puede limpiar correctamente su GPU. 
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ab
                | Usando rfagas de aire controladas, elimine la acumulacin de suciedad significativa sobre y alrededor de su tarjeta grfica. 
              li(style="margin-bottom: 2px")
                i.fas.fa-check-circle.t-ab
                | Un pao de microfibra puede ayudar a recolectar grandes trozos de polvo que caen entre las rfagas de aire.

    .row.mb-5
      .col-auto(data-aos="fade-right")
        .BGS01.px-5.py-2
          .h3.mb-0 Mantenimiento de CPU

    .row.justify-content-center.mb-4
      .col-lg-5.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema3/img58.svg' alt="Imagen decorativa")
      .col-lg-7
        ul.lista-ul--color.lista-ul--separador
          li(style="margin-bottom: 2px" data-aos="fade-left")
            i.fas.fa-check-circle.t-ab
            | El lugar que se limpia con menos frecuencia en una computadora es la CPU.
          li(style="margin-bottom: 2px" data-aos="fade-left")
            i.fas.fa-check-circle.t-ab
            | Esto se debe a que el procesador suele estar cubierto por un dispositivo de refrigeracin y no se puede acceder a l sin desmontarlo.
          li(style="margin-bottom: 2px" data-aos="fade-left")
            i.fas.fa-check-circle.t-ab
            | Otra razn por la que el mantenimiento de la CPU rara vez se completa es que solo se necesita cada pocos aos.
          li(style="margin-bottom: 2px" data-aos="fade-left")
            i.fas.fa-check-circle.t-ab
            | Es poco lo que puede hacer con una CPU adems de volver a aplicar un compuesto trmico y reinstalar el enfriador.
          li(style="margin-bottom: 2px" data-aos="fade-left")
            i.fas.fa-check-circle.t-ab
            | Reemplazar la pasta trmica al menos cada dos aos ayudar significativamente a aumentar la vida til de su procesador.
          li(style="margin-bottom: 2px" data-aos="fade-left")
            i.fas.fa-check-circle.t-ab
            | Cuando vuelva a aplicar pasta trmica, primero deber quitar el dispositivo de enfriamiento conectado a su procesador.
          li(style="margin-bottom: 2px" data-aos="fade-left")
            i.fas.fa-check-circle.t-ab
            | Nunca intente separar una CPU y un enfriador cuando el dispositivo est fro o no se haya encendido recientemente.
        
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-7.order-lg-1.order-2
        ul.lista-ul--color.lista-ul--separador
